.item {
    display: flex;
    align-items: center;
    width: 400px;

    padding: 8px 10px;
    box-shadow: 1px 1px 10px;
    border-radius: 2px;
    overflow: hidden;
}

.status {
    display: inline-block;
    background-color: red;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    margin-right: 17px;
}

.status.online {
    background-color: green;
}

.avatar {
    display: inline-block;
    width: 97px;
    margin-right: 15px;
}

.name {
    font-size: 24px;
    font-weight: 600;
}